.profile{
    margin-top: calc(60 / 1920 * 100vw);
    margin-inline: calc(100 / 1920 * 100vw);
}

.profile-card{
    margin-top: calc(30 / 1920 * 100vw);
    display: flex;
}

.profile-card-content{
    display: grid;
    margin-left: calc(30 / 1920 * 100vw);
    grid-template-columns: 1fr 1fr;
    gap: calc(30 / 1920 * 100vw);
}

@media (max-widht: 1440px) {
    .profile{
        margin-inline: calc(70 / 1920 * 100vw);
    }
}


@media (max-width: 820px) {
    .profile-card{
        margin-top: calc(30 / 640 * 100vw);
        display: block;
    }

    .profile-card-item {
        display: block;
        margin-top: 0;
    }

    .profile-card-content{
        margin-left: 0;
        margin-top: calc(20 / 640 * 100vw);
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: calc(30 / 640 * 100vw);
    }

    .profile-card-item-2{
        margin-top: calc(20 / 640 * 100vw);
        margin-inline: 0;
    }
}

@media (max-width: 420px) {
    .profile-card-content{
        margin-left: 0;
        margin-top: calc(20 / 420 * 100vw); 
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;

        grid-gap: calc(20 / 420 * 100vw);
    }
}