.contact{
    position: fixed;
    z-index: 100;
    left: 0;
    bottom: 30%;

    display: flex;
    align-items: center;
}

.contact-content{
    display: flex;
    flex-direction: column;
}

.contact-link{
    transition-duration: 300ms;
    opacity: 0.5;
}

.contact-link:hover{
    opacity: 1;
}

@media (max-width: 430px) {
    .contact{
        display: none;
    }
}