.swiper-pagination-bullet {
    opacity: 1 !important; 
    background-color: var(--main-gray) !important;
}
.swiper-pagination-bullet-active {
    transition-duration: 300ms;
    padding: 5px;
    background: var(--main-pink) !important;
}

.eduction{
    margin-inline: calc(100 / 1920 * 100vw);
    margin-top: calc(40 / 1920 * 100vw);
}

.eduction-subtitle{
    margin-top: calc(40 / 1920 * 100vw);
}

.eduction-card{
    display: flex;
    justify-content: space-between;
    margin-top: calc(20 / 1920 * 100vw);
    margin-bottom: calc(20 / 1920 * 100vw);
}

.eduction-corses{
    display: flex;
    justify-content: space-between;
    margin-top: calc(20 / 1920 * 100vw);
}

.education-courses-slider{
    margin-block: calc(20 / 1920 * 100vw);
}

.education-courses-slider__s{
    margin-block: calc(40 / 1920 * 100vw);
}

.item-cor-1{
    display: flex;
    justify-content: space-between;
}

.item-cor-1-img{
    margin-inline: 200px;
}

@media (max-width: 1440px) {
    .eduction{
        margin-inline: calc(100 / 1920 * 100vw);
    }
}

@media (max-width: 820px) {
    .eduction{
        margin-inline: 0;
    }

    .eduction-card{
        display: flex;
        flex-direction: column;
        margin-top: calc(20 / 640 * 100vw);
    }


    .eduction-corses{
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: calc(20 / 640 * 100vw);
    }

    .education-courses-slider{
        margin-block: calc(20 / 640  * 100vw);
    }
    
    .education-courses-slider__s{
        margin-block: calc(20 / 640  * 100vw);
    }
    
}


@media (max-width: 420px) {
    .eduction-card{
        margin-bottom: calc(20 / 420 * 100vw);
    }

    .eduction-corses{
        margin-top: calc(20 / 420 * 100vw);
    }

    .education-courses-slider{
        margin-block: calc(20 / 420  * 100vw);
    }
    
    .education-courses-slider__s{
        margin-block: calc(20 / 420  * 100vw);
    }
    
}